:root {
  --primary-blue: hsl(219, 29%, 15%);
  --secondary-blue: hsl(227, 89%, 85%);
  --accent-orange: hsl(31, 100%, 67%);
  --accent-green: hsl(87, 46%, 63%);
  --accent-purple: hsl(260, 54%, 67%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--primary-blue);
  color: var(--secondary-blue);
}

h1,
h2,
h3 {
  margin: 0.9375rem;
  margin-bottom: 0;
}

h1 {
  font-size: 2.25rem;
  font-weight: 700;
}
@media (min-width: 64em) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.25rem;
  font-weight: 500;
  margin-top: 0.3125rem;
}

p {
  margin: 0.9375rem;
  font-size: 0.875rem;
}

a,
a:visited,
a:active {
  margin: 0.9375rem;
  text-decoration: none;
}

main {
  padding: 0 6rem;
  max-width: 69.375rem;
}
@media (min-width: 64em) {
  main {
    padding: 0 6rem;
  }
}

.section {
  height: 100vh;
}

.header {
  margin-top: 1.625rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.title {
  display: flex;
  flex-direction: column;
  margin-top: 12vh;
  gap: 3rem;
  overflow: hidden;
  max-width: 45%;
}
.title__header .location {
  display: flex;
  align-items: center;
  margin: 10px;
}
.title__nav {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 15vh;
}

.footer__social {
  height: 24px;
}
.footer__social--github, .footer__social--linkedin {
  filter: invert(100%) sepia(3%) saturate(12%) hue-rotate(103deg) brightness(105%) contrast(105%);
}